$(function () {

	// smooth scroll
	$('a[href*="#"]:not([href="#"])').click(function() {
        if (location.pathname.replace(/^\//, '') == this.pathname.replace(/^\//, '') && location.hostname == this.hostname) {
            var target = $(this.hash);
            target = target.length ? target : $('[name=' + this.hash.slice(1) + ']');
            if (target.length) {
                $('html, body').animate({
                    scrollTop: target.offset().top - 90
                }, 1000);
                return false;
            }
        }
    });

	// ddslick
	$('#choose-lang').ddslick({
		width: 160
	});
	$('#demo-select-1').ddslick({
		width: 160
	});
	$('#demo-select-2').ddslick({
		width: 160
	});

	$(this).scrollTop(0);

	// cookie 
	if (!$.cookie('was')) {
		$('#cookie').show();
		console.log('hello')
	}
	$('#cookie-accept').on('click', function() {
		$('#cookie').hide();
	});
	$.cookie('was', true, {
		expires: 365,
		path: '/'
	});

	// input padding with button
	$('.input-group').each(function() {
		$(this).find('input:not([type="submit"]').css('padding-right', $(this).find('.btn').outerWidth() + 16);
		$(this).find('textarea').css('padding-bottom', $(this).find('.btn').outerHeight() + 16);
	});

	// change image after hover
	$('[data-hover]').mouseenter(function() {
		var hover = $(this).data('hover');

		$('[data-hover-image]').removeClass('is-active');
		$('[data-hover-image=' + hover + ']').addClass('is-active');
	});

	// change image after click
	$('[data-click]').on('click', function() {
		var click = $(this).data('click');

		$(this).closest('[data-click-parent]').find('[data-click]').removeClass('is-active');
		$(this).addClass('is-active');

		$(this).closest('[data-click-parent]').find('[data-click-image]').removeClass('is-active');
		$('[data-click-image=' + click + ']').addClass('is-active');
	});

	// tabs
	$('[data-tab]').on('click', function() {
		var tab = $(this).data('tab');

		$('[data-tab]').removeClass('is-active');
		$(this).addClass('is-active');

		$('[data-tab-content]').removeClass('is-active');
		$('[data-tab-content=' + tab + ']').addClass('is-active');
	});

	// mobile menu
	$('.header__call').on('click', function() {
		$('body').toggleClass('no-scroll');
		$('#menu').toggleClass('is-active');
		$('#overlay').toggleClass('is-active');
	});
	$('#overlay').on('click', function() {
		$('body').toggleClass('no-scroll');
		$('#menu').toggleClass('is-active');
		$(this).toggleClass('is-active');
	});
	$('.expand-nav').on('click', function() {
		$(this).toggleClass('open');
	});

	// accordion
	$('.toggle').click(function(e) {
		e.preventDefault();
		var $this = $(this);
		if ($this.next().hasClass('is-show')) {
			$this.next().removeClass('is-show');
			$this.next().slideUp(350);
			$this.removeClass('is-active');
		} else {
			$this.parent().parent().find('a.toggle').removeClass('is-active');
			$this.parent().parent().find('li .inner').removeClass('is-show');
			$this.parent().parent().find('li .inner').slideUp(350);
			$this.next().toggleClass('is-show');
			$this.next().slideToggle(350);
			$this.toggleClass('is-active');
		}
	});
});

// wow init
new WOW().init();

// header sticky
$(window).scroll(function() {
    if ($(this).scrollTop() > 60) {
        $('#header').addClass("sticky");
    } else {
        $('#header').removeClass("sticky");
    }
});